html,
body {
  height: 100%;
  margin: 0;
}

body {
  font-family: 'PT Mono', serif;
  overflow-x: hidden;
}

#app {
  height: 100%;
}

::-webkit-scrollbar {
  width: 5px;
  background: transparent;
}
::-webkit-scrollbar-thumb {
  width: 15px;
  height: 5px;
  background-color: #ccc1a7;
}



